.main {
  -webkit-user-select: none;

  position: fixed;
  top: 0;
  left:0;
  height: 0;
  width: 0;

  .contextMenu {
    position: absolute;
    z-index: 200;
    background: white;
    border: 1px solid blue;
    //padding: 3px 3px;

    cursor: default;

    div {
      white-space: nowrap;
      padding: 0 20px;
    }

    div:hover {
      background: lightgray;
    }
  }
}