@import '../../../../00_common/01_scss/variables';

.main {
  display: grid;
  //grid-template-columns: $timelineWidth auto $timelineWidth $scrollSize;

  //grid-template-columns: $timelineWidth calc(100vw - (#{$sidebarWidth} + #{$timelineWidth} + #{$timelineWidth} + #{$scrollSize})) $timelineWidth $scrollSize;

  .gap {
    background: limegreen;
  }
}


.withoutSidePanel {
  grid-template-columns: $timelineWidth calc(100vw - (#{$sidebarWidth} + #{$timelineWidth} + #{$timelineWidth} + #{$scrollSize})) $timelineWidth $scrollSize;
}

.withSidePanel {
  grid-template-columns: $timelineWidth calc(100vw - (#{$sidebarWidth} + #{$sidePanelWidth} + #{$timelineWidth} + #{$timelineWidth} + #{$scrollSize})) $timelineWidth $scrollSize;
}



