@import '../../../../../00_common/01_scss/variables';

.main {
  //-webkit-user-select: text;

  background: khaki;
  //width: calc(100vw - (#{$sidebarWidth} + #{$timelineWidth} + #{$timelineWidth} + #{$scrollSize}));
  overflow-x: hidden;
  position: relative;

  .selectWindowBody {
    position: absolute;
    background: green;
    opacity: 0.3;
    z-index: 2000;
  }

  .dragZone {
    background: cyan;
    position: absolute;
    opacity: 0;
  }

  .content {
    -webkit-user-select: text;

    display: flex;
    //height: 100%;
    position: relative;
  }
}